%organism-vote{    
    @extend %molecule-home-section;

    h2:before{
        background-image: url( svg-bg-uri($svg-wave, #dddddd) );
    }

    .rte{
	    font-size: 1.8rem;
	    color: #5c5c5c;
	    text-align: center;

	    .error{
	    	background-image: url( svg-bg-uri($svg-exclamation, #5c5c5c) );
		    background-repeat: no-repeat;
		    background-size: 20px;
		    background-position: 15px 15px;
		    background-color: #FFDDDE;
		    padding: 15px 20px;
		    width: 85%;
		    margin: auto;
		    margin-bottom: 25px;

		    strong{
		    	font-weight: 600;
		    }
	    }

	    .main-response{
	        margin-bottom: 20px;

	        a{
	        	color: #31455d;
	        	font-weight: 600;
	        	text-decoration: underline;
	        }
	    }

	    .incomplet{
	    	padding-top: 10px;
	    	font-weight: 600;
	    }

	    .warning{
	        margin-bottom: 20px;
	    }

	    .poll-address{
	        margin-bottom: 20px;

	    	.poll-number{
	    		font-weight: 600;
	    	}
	    }

	    .add-favorites{
		    width: max-content;
		    margin: auto;
		    margin-bottom: 20px;
	    }

	}

    margin-bottom: 40px;
}
